<template>
  <register-info-title-fill class="candidate-personal-info" title="个人信息">
    <register-info
      :infoList="personalInfoList"
      identity="candidate"
      registerBtn
      @handle-register="handleRegister"
    ></register-info>
  </register-info-title-fill>
</template>

<script>
import { RegisterInfoTitleFill, RegisterInfo } from "./components";
import * as localUtils from "./utils";

export default {
  name: "RegisterCandidate",
  components: {
    RegisterInfoTitleFill,
    RegisterInfo
  },
  data() {
    return {
      personalInfoList: [
        {
          id: 1,
          type: "input",
          title: "姓名",
          class: "name",
          placeholder: "请填写您的真实姓名",
          model: "",
          keyName: "name"
        },
        {
          id: 2,
          type: "radio",
          title: "性别",
          class: "gender",
          model: 0,
          keyName: "gender",
          data: [
            {
              id: 1,
              label: 0,
              content: "男"
            },
            {
              id: 2,
              label: 1,
              content: "女"
            }
          ]
        },
        {
          id: 3,
          type: "input",
          title: "手机",
          class: "phone",
          placeholder: "请填写您的手机号码",
          model: "",
          keyName: "tel"
        }
      ],
      emial: ""
    };
  },
  created() {
    this.email = localUtils.analyseUrlQueryEmail(this);
  },
  methods: {
    handleRegister() {
      const data = {
        roleId: 2,
        emial: this.emial
      };
      this.personalInfoList.forEach(item => {
        data[item.keyName] = item.model;
      });
      localUtils.addMoreInfo(data, this);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./components/mixin.scss";

.candidate-personal-info {
  @extend .e-register-info;
}
</style>
