<template>
  <div class="common-card">
    <el-card class="card">
      <div class="header">
        <!-- 根据不同进度有不同的颜色显示 -->
        <div>
          <span :class="['progress', progressColor]">{{
            cardInfo.progress
          }}</span>
        </div>
        <span class="name" v-if="type === 'interviewer'">{{
          cardInfo.name
        }}</span>
        <div>
          <span class="time">截止时间：{{ cardInfo.interviewTime }}</span>
        </div>
      </div>
      <div class="main">
        <span>{{ cardInfo.job }}</span>
      </div>
      <ul class="footer">
        <li class="footer-item" v-for="item of operationList" :key="item.id">
          {{ item.content }}
        </li>
      </ul>
    </el-card>
  </div>
</template>
<script>
const operationList = {
  interviewer: ["简历查看", "未出题", "发起面试"],
  hr: ["管理申报表", "查看详情", "编辑", "删除"]
};
export default {
  name: "commonCard",
  components: {},
  props: {
    cardInfo: Object,
    type: String
  },
  data() {
    return {};
  },
  computed: {
    operationList() {
      return operationList[this.type].map((item, index) => {
        return {
          id: index,
          content: item
        };
      });
    },
    progressColor() {
      const colorMap = {
        招聘中: "blue",
        已结束: "gray"
      };
      return colorMap[this.cardInfo.progress] || "green";
    }
  }
};
</script>

<style lang="scss" scoped>
.common-card {
  /deep/ .el-card__body {
    padding: 30px 30px 20px;
  }
  .header {
    @include flex(all, space-between);
    & > div {
      width: 40%;
    }
    & > div:last-child {
      text-align: right;
    }
    .name {
      flex: 1;
      text-align: center;
    }
    .progress {
      text-align: center;
      box-sizing: border-box;
      padding: 5px 20px;
      border-radius: $largeBR;
    }
    .time {
      font-size: $smallFS;
      color: $secondaryFC;
    }
    .green {
      border: 1px solid $successGreen;
      color: $successGreen;
    }
    .blue {
      border: 1px solid $primaryBlue;
      color: $primaryBlue;
    }
    .gray {
      border: 1px solid $placeholderFC;
      color: $placeholderFC;
    }
  }
  .main {
    font-size: $mediumFS;
  }
  .header,
  .main {
    margin-bottom: 25px;
  }
  .footer {
    text-align: right;
    &-item {
      display: inline-block;
      margin-left: 30px;
      font-size: $smallFS;
      cursor: pointer;
      &:hover {
        color: $primaryBlue;
      }
    }
  }
}
</style>
