<template>
  <div class="register-info">
    <title-fill
      v-for="infoItem in infoList"
      :class="infoItem.class"
      :title="infoItem.title"
      width="15%"
      weight="bold"
      :key="infoItem.id"
    >
      <el-input
        v-if="infoItem.type === 'input'"
        :placeholder="infoItem.placeholder"
        v-model="infoItem.model"
      />
      <el-radio
        v-else-if="infoItem.type === 'radio'"
        v-model="infoItem.model"
        v-for="item in infoItem.data"
        :key="item.id"
        :label="item.label"
        >{{ item.content }}</el-radio
      >
    </title-fill>
    <div v-if="registerBtn" class="register-btn">
      <el-button type="primary" @click="handleRegister">立即注册</el-button>
    </div>
  </div>
</template>

<script>
import { TitleFill } from "@/components";

export default {
  name: "RegisterInfo",
  components: {
    TitleFill
  },
  props: {
    infoList: Array,
    registerBtn: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleRegister() {
      this.$emit("handle-register");
    }
  }
};
</script>

<style lang="scss" scoped>
.register-info {
  .name,
  .gender,
  .phone,
  .identity,
  .position,
  .company-name,
  .company-address {
    margin-top: 30px;
  }
  .register-btn {
    margin: 40px 0;
    text-align: center;
    button {
      width: $extraLargeBW;
    }
  }
}
</style>
