<template>
  <div class="register-step-tips">
    <span :class="['check-email', { current: current === 'email' }]">
      1.邮箱验证
    </span>
    <span class="line">————</span>
    <span :class="['account-info', { current: current === 'account' }]">
      2.完善账号信息
    </span>
    <span class="line">————</span>
    <span :class="['register-success', { current: current === 'success' }]">
      3.注册成功
    </span>
  </div>
</template>

<script>
export default {
  name: "RegisterStepTips",
  components: {},
  computed: {
    current() {
      const currentMap = {
        "/register/enterprise": "account",
        "/register/candidate": "account",
        "/register/success": "success",
        "/register/third-party": "account"
      };
      return currentMap[this.$route.path] || "email";
    }
  }
};
</script>

<style lang="scss" scoped>
.register-step-tips {
  @include flex();
  & > span {
    margin-right: 10px;
  }
  .current {
    font-size: $mediumFS;
    color: $primaryBlue;
  }
}
</style>
