<template>
  <div class="hr-job-list">
    <job-item
      @click.native="handleClick($event)"
      class="hr-job-item"
      v-for="job of jobList"
      :key="job.id"
      :cardInfo="job"
      type="hr"
    ></job-item>
  </div>
</template>

<script>
import JobItem from "@/views/components/interviewer/CommonCard";

export default {
  name: "",
  components: {
    JobItem
  },
  props: {
    jobList: Array
  },
  methods: {
    handleClick(e) {
      const target = e.target;
      if (!target) {
        return;
      }
      if (target.tagName.toLowerCase() === "li") {
        const funcMap = {
          管理申报表: this.handleManageDeclaration,
          查看详情: this.handleViewDetails,
          编辑: this.handleEdit,
          删除: this.handleDelete
        };
        funcMap[target.innerText]();
      }
    },
    handleManageDeclaration() {
      this.$router.push("/hr/filter");
    },
    handleViewDetails() {
      this.$router.push("/hr/details");
    },
    handleEdit() {
      this.$router.push("/hr/edit");
    },
    handleDelete() {
      console.log("删除");
    }
  }
};
</script>

<style lang="scss" scoped>
.hr-job-list {
  .hr-job-item {
    margin-top: 10px;
  }
}
</style>
