<template>
  <div class="person-data-edit">
    <div class="avatar">
      <el-avatar :size="140" alt="头像编辑" :src="src"></el-avatar>
    </div>
    <title-fill class="name" title="姓名" :width="width">
      <el-input v-model="name" clearable> </el-input>
    </title-fill>
    <title-fill class="gender" title="性别" :width="width">
      <el-radio v-model="gender" label="1">男</el-radio>
      <el-radio v-model="gender" label="2">女</el-radio>
    </title-fill>
    <title-fill class="phone" title="手机" :width="width">
      <el-input v-model="phone" clearable> </el-input>
    </title-fill>
    <div class="btn">
      <el-button type="primary" size="small">编辑</el-button>
      <el-button size="small">取消</el-button>
    </div>
  </div>
</template>

<script>
import { TitleFill } from "@/components";

export default {
  name: "",
  components: {
    TitleFill
  },
  props: {
    src: String,
    propWidth: {
      type: String,
      default: "15%"
    }
  },
  data() {
    return {
      width: this.propWidth,
      name: "",
      gender: "",
      phone: ""
    };
  }
};
</script>

<style lang="scss" scoped>
.person-data-edit {
  & > div:not(:last-child) {
    margin-bottom: 30px;
  }
  .avatar,
  .btn {
    text-align: center;
  }
  .btn {
    button {
      width: 80px;
      margin: 0 20px;
    }
  }
}
</style>
