<template>
  <div class="resume-title-fill">
    <div class="header">
      <span class="title">{{ title }}</span>
      <div v-if="readType === 'edit'">
        <i :data-title="title" class="edit el-icon-edit-outline"></i>
      </div>
      <div v-else-if="readType === 'add'">
        <el-button
          :data-title="title"
          class="add"
          icon="el-icon-plus"
          size="small"
          >添加{{ title }}</el-button
        >
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "ResumeTitleFill",
  components: {},
  props: {
    title: String,
    type: String,
    edit: Boolean
  },
  data() {
    return {
      dialogShow: this.edit || false
    };
  },
  watch: {
    edit(val) {
      if (val) {
        this.dialogShow = true;
      }
    }
  },
  computed: {
    readType() {
      return (!this.$attrs.readOnly && this.type) || "read";
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./resume.scss";

.resume-title-fill {
  .header {
    @include flex(all, space-between, center);
    margin-bottom: 10px;
    .title {
      font-size: $largeFS;
      font-weight: 500;
    }
    .edit {
      @extend .extend-resume-item-edit;
    }
  }
}
</style>
