<template>
  <div class="new-tag">
    <el-tag
      :key="tag"
      v-for="tag in dynamicTags"
      closable
      :disable-transitions="false"
      @close="handleClose(tag)"
    >
      {{ tag }}
    </el-tag>
    <el-input
      class="input-new-tag"
      v-if="inputVisible"
      v-model="tagValue"
      ref="saveTagInput"
      size="small"
      @keyup.enter.native="handleInputConfirm"
      @blur="handleInputConfirm"
    >
    </el-input>
    <el-button v-else class="button-new-tag" size="small" @click="showInput">{{
      newBtn
    }}</el-button>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {
    // 传进来的标签
    tags: {
      type: Array,
      default: () => []
    },
    newBtn: {
      type: String,
      default: "+ New Tag"
    }
  },
  data() {
    return {
      dynamicTags: [...this.tags],
      inputVisible: false,
      tagValue: ""
    };
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let tagValue = this.tagValue;
      if (tagValue) {
        this.dynamicTags.push(tagValue);
      }
      this.inputVisible = false;
      this.tagValue = "";
    }
  }
};
</script>

<style lang="scss" scoped>
.new-tag {
  line-height: 40px;
  .el-tag {
    margin-right: 10px;
  }
  .button-new-tag {
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    vertical-align: bottom;
  }
}
</style>
