<template>
  <div class="question-common-table">
    <el-table
      :data="tableData"
      style="width: 100%"
      @row-click="handleRowClick"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        type="selection"
        width="50"
        v-if="hasSelect"
      ></el-table-column>
      <el-table-column
        v-for="item in tableSign"
        :key="item.id"
        :prop="item.prop"
        :label="item.label"
        align="center"
        width="100"
      ></el-table-column>
      <el-table-column align="center" label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click.stop="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "questionCommonTable",
  components: {},
  props: {
    tableData: Array,
    hasSelect: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tableSign: [
        {
          id: 11,
          prop: "questionName",
          label: "题名"
        },
        {
          id: 2,
          prop: "starGrade",
          label: "星级"
        },
        {
          id: 3,
          prop: "average",
          label: "平均分"
        },
        {
          id: 4,
          prop: "frequency",
          label: "出现频率"
        }
      ],
      multipleSelection: [],
      multipleSelectionId: []
    };
  },
  watch: {
    multipleSelection: function() {
      let arr = [];
      for (let i in this.multipleSelection) {
        arr.push(this.multipleSelection[i].id);
      }
      this.multipleSelectionId = arr;
      console.log(this.multipleSelectionId);
    }
  },
  methods: {
    handleDelete(row) {
      console.log(row.id);
    },
    handleRowClick(row) {
      this.$emit("clickTableRowId", row.id);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
};
</script>

<style lang="scss" scoped>
.question-common-table {
  /deep/ .el-table__row {
    cursor: pointer;
  }
  /deep/ .el-table__row:nth-child(odd) {
    background: oldlace;
  }
  /deep/ .el-table__row:nth-child(even) {
    background: #f0f9eb;
  }
}
</style>
