<template>
  <div class="register-main">
    <div class="identity">
      <el-radio v-model="radio" label="0">求职者</el-radio>
      <el-radio v-model="radio" label="1">企业</el-radio>
      <el-radio v-model="radio" label="2">第三方</el-radio>
    </div>
    <el-input class="email" type="user" placeholder="邮箱" v-model="email" />
    <div class="verify-code">
      <el-input placeholder="邮箱验证码" v-model="verifyCode" />

      <el-button
        v-if="btnClick"
        class="get-code"
        type="info"
        disabled
        plain
        @click="getEmailCode"
        >{{ emailCountDown }}s</el-button
      >
      <el-button
        v-else
        class="get-code"
        type="primary"
        plain
        @click="getEmailCode"
        >获取验证码</el-button
      >
    </div>
    <el-input
      class="pwd"
      show-password
      placeholder="密码，不少于6位"
      v-model="pwd"
    />
    <el-input
      class="check-pwd"
      show-password
      placeholder="确认密码"
      v-model="confirmPwd"
    />
    <el-button class="next-btn" type="primary" @click="handleNext">
      下一步
    </el-button>
  </div>
</template>

<script>
export default {
  name: "RegisterMain",
  components: {},
  data() {
    return {
      radio: "0",
      email: "",
      verifyCode: "",
      btnClick: false,
      pwd: "",
      confirmPwd: "",
      emailCountDown: 10
    };
  },
  methods: {
    handleNext() {
      if (this.pwd !== this.confirmPwd) {
        this.$message.warning("两次密码输入不一致");
        return;
      }
      const data = {
        verifyCode: this.verifyCode,
        pwd: this.pwd,
        confirmPwd: this.confirmPwd
      };
      this.$axios
        .verifyPwd(data)
        .then(res => {
          console.log(res);
          this.$message.success("验证成功，请完善相关信息");
          this.routeNext();
        })
        .catch(() => {
          this.$message.warning("验证失败");
        });
    },
    getEmailCode() {
      const data = {
        email: this.email
      };
      this.$axios
        .verifyEmail(data)
        .then(res => {
          console.log(res);
          this.btnClick = true;
          this.$message.success("验证码发送成功，请在邮箱查收");
          this.handleEmailCodeBtnStatus();
        })
        .catch(err => {
          this.$message.warning(err.msg);
        });
    },
    handleEmailCodeBtnStatus() {
      let time = setInterval(() => {
        this.emailCountDown -= 1;
        if (this.emailCountDown === 0) {
          clearInterval(time);
          this.btnClick = false;
          this.emailCountDown = 10;
        }
      }, 1000);
    },
    routeNext() {
      let identityMap = [
        {
          path: "candidate",
          name: "求职者注册"
        },
        {
          path: "enterprise",
          name: "企业注册"
        },
        {
          path: "third-party",
          name: "第三方注册"
        }
      ];
      this.$router.push({
        path: `/register/${identityMap[this.radio].path}`,
        query: { email: this.email }
      });
    }
  },
  computed: {
    emailCodeBtnDisabled() {
      return this.getEmailCodeBtn !== "获取验证码";
    }
  }
};
</script>

<style lang="scss" scoped>
.register-main {
  margin-top: 40px;
  @include flex(align-items, center);
  flex-direction: column;
  .identity {
    text-align: center;
  }
  .identity,
  .email,
  .verify-code,
  .pwd,
  .check-pwd {
    width: 40%;
  }

  .identity,
  .email,
  .verify-code,
  .pwd,
  .check-pwd,
  .next-btn {
    margin-bottom: 30px;
  }
  .verify-code {
    @include flex(justify-content, space-between);
    .get-code {
      width: 40%;
      margin-left: 10px;
    }
  }
  .next-btn {
    width: $extraLargeBW;
  }
}
</style>
