<template>
  <div class="upload">
    <el-upload
      class="upload-avatar"
      action="https://localhost:8080/api/images/"
      :show-file-list="false"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload"
    >
      <img v-if="imageUrl" :src="imageUrl" class="avatar" />
      <i v-else class="el-icon-plus upload-avatar-icon"></i>
    </el-upload>
    <div class="tip">点击图片/方框上传头像</div>
  </div>
</template>

<script>
export default {
  name: "UploadAvatar",
  components: {},
  props: {},
  data() {
    return {
      imageUrl: require("@/assets/images/pika.jpg")
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style lang="scss" scoped>
.upload {
  .upload-avatar {
    $imgSize: 150px;
    /deep/ .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      &:hover {
        border-color: #409eff;
      }
    }
    .upload-avatar-icon {
      font-size: 28px;
      color: #8c939d;
      width: $imgSize;
      height: $imgSize;
      line-height: $imgSize;
      text-align: center;
    }
    .avatar {
      width: $imgSize;
      height: $imgSize;
      display: block;
    }
  }
  .tip {
    margin-top: 5px;
    text-align: center;
    color: $secondaryFC;
    font-size: $smallFS;
  }
}
</style>
