<template>
  <div class="hr">
    <Header :titleContent="headerList" :active="active">
      <el-dropdown>
        <el-avatar :size="40" alt="头像" :src="avatarUrl" />
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            icon="el-icon-user"
            @click.native="handleEnterCenter"
            >个人中心</el-dropdown-item
          >
          <el-dropdown-item
            icon="el-icon-switch-button"
            @click.native="handleOut"
            >登出</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </Header>
    <div class="main">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Header from "@/views/components/home/HomeHeader";

export default {
  name: "Hr",
  components: {
    Header
  },
  data() {
    return {
      avatarUrl: require("@/assets/images/pika.jpg"),
      headerList: [
        {
          id: 1,
          title: "首页",
          route: "/hr"
        },
        {
          id: 2,
          title: "面试",
          route: "/hr/manage"
        },
        {
          id: 3,
          title: "简历",
          route: "/hr/resume"
        },
        {
          id: 4,
          title: "题库",
          route: "/hr/question"
        },
        {
          id: 5,
          title: "个人中心",
          route: "/hr/center"
        }
      ],
      active: "1"
    };
  },
  methods: {
    handleEnterCenter() {
      this.$router.push("/hr/center");
    },
    handleOut() {
      this.$message.success("成功退出系统");
    }
  },
  watch: {
    $route: {
      handler(to) {
        // 路由对应 保持active正常
        const routeMap = [
          ["/hr", "hr/edit"],
          ["/hr/manage", "/hr/filter", "/hr/interviewing", "/hr/finish"],
          ["/hr/resume"],
          ["/hr/question"],
          ["/hr/center"]
        ];
        let index = routeMap.findIndex(arr => arr.includes(to.path));
        this.active = index === -1 ? "1" : this.headerList[index].id + "";
      },
      immediate: true
    }
  }
};
</script>

<style lang="scss" scoped>
.hr {
  .main {
    width: $centerWidth;
    margin: $headerHeight + 10px auto;
  }
}
</style>
