<template>
  <el-card
    class="home-job-item"
    title="查看岗位详情"
    @click.native="handleClick"
  >
    <div class="title">
      <span class="position">{{ posName }}</span>
      <span class="company">{{ company }}</span>
    </div>
    <div class="info">
      <ul class="info-list">
        <li class="info-item" v-for="info of infoList" :key="info.id">
          {{ info.content }}
        </li>
      </ul>
      <span class="info-scale">{{ scale }}</span>
    </div>
    <div class="welfare">
      <ul class="welfare-list">
        <li
          class="welfare-item"
          v-for="welfare of welfareList"
          :key="welfare.id"
        >
          {{ welfare.content }}
        </li>
      </ul>
      <el-button
        class="apply-btn"
        type="primary"
        size="small"
        @click.stop="handleApply"
        title="点击申请岗位"
        >申请岗位</el-button
      >
    </div>
  </el-card>
</template>

<script>
export default {
  name: "HomeJobItem",
  components: {},
  data() {
    return {
      posName: "web前端开发",
      company: "哗啦啦有限公司",
      scale: "10000人以上",
      infoList: [
        {
          id: 1,
          content: "13k-15k"
        },
        {
          id: 2,
          content: "深圳"
        },
        {
          id: 3,
          content: "经验不限"
        },
        {
          id: 4,
          content: "学历不限"
        }
      ],
      welfareList: [
        {
          id: 1,
          content: "五险一金"
        },
        {
          id: 2,
          content: "工作环境好"
        },
        {
          id: 3,
          content: "同事很nice"
        },
        {
          id: 4,
          content: "人际关系好"
        },
        {
          id: 5,
          content: "氛围活跃"
        }
      ]
    };
  },
  methods: {
    handleApply() {
      this.$message.success("已经申请");
    },
    handleClick() {
      this.$router.push("/candidate/details");
    }
  }
};
</script>

<style lang="scss" scoped>
.home-job-item {
  cursor: pointer;
  margin-top: 10px;
  div:not(:first-child) {
    margin-top: 20px;
  }
  .title {
    @include flex(all, space-between, center);
    .position {
      font-size: $largeFS;
      font-weight: bold;
    }
  }
  .info {
    @include flex(all, space-between, center);
    &-list {
      overflow: hidden;
      .info-item {
        float: left;
        height: 25px;
        line-height: 25px;
        margin-right: 20px;
        &:first-child {
          color: $warningBrown;
          font-size: $largeFS;
        }
      }
    }
  }
  .welfare {
    @include flex(all, space-between, center);
    &-list {
      overflow: hidden;
      .welfare-item {
        float: left;
        height: 25px;
        line-height: 25px;
        padding: 0 10px;
        margin-right: 20px;
        color: $secondaryFC;
        border: $normalBS solid $firstBC;
        border-radius: $normalBR;
      }
    }
  }
}
</style>
