<template>
  <el-card class="interviewing-item">
    <div class="header">
      <span class="name">姓名：{{ interviewData.name }}</span>
      <span class="phone">手机号码：{{ interviewData.phone }}</span>
      <span class="school">学校：{{ interviewData.school }}</span>
    </div>
    <div class="main">
      <span class="interviewer">
        <span class="interviewer-text">分配面试官</span>
        <Select
          class="interviewer-select"
          size="small"
          placeholder="未分配面试官"
          :options="options"
          :propValue="interviewer"
        />
      </span>
      <span class="state">
        {{ interviewData.state }}
      </span>
    </div>
    <div class="footer">
      <span class="footer-text">选择面试时间</span>
      <el-date-picker
        class="footer-select"
        v-model="interviewData.date"
        :default-value="interviewData.date"
        type="date"
        placeholder="选择面试时间"
        size="small"
      >
      </el-date-picker>
    </div>
  </el-card>
</template>

<script>
import { Select } from "@/components";

export default {
  name: "",
  components: {
    Select
  },
  props: {
    interviewData: Object
  },
  data() {
    return {
      options: [
        {
          value: "选项1",
          label: "面试官1"
        },
        {
          value: "选项2",
          label: "面试官2"
        }
      ]
    };
  },
  computed: {
    interviewer() {
      const interviewer = this.interviewData.interviewer;
      if (interviewer) {
        return interviewer;
      } else {
        return "";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.interviewing-item {
  .header,
  .main {
    margin-bottom: 20px;
  }
  .header {
    @include flex(all, space-between);
    .name,
    .school {
      $width: 20%;
      width: $width;
      @include single-line-ellipsis($width);
    }
    .school {
      text-align: right;
    }
  }
  .main {
    @include flex(all, space-between);

    .state {
      color: $successGreen;
    }
  }
  .footer,
  .interviewer {
    width: 50%;
    @include flex(align-items);
    &-text {
      width: 30%;
    }
    &-select {
      flex: 1;
    }
  }
}
</style>
