<template>
  <li class="interview-manage-item">
    <div class="position">{{ jobInfo.position }}</div>
    <div class="data">
      <div class="distribution">已分配/未分配面试官：{{ distribution }}</div>
      <div class="interview">初筛中/面试中/已完成：{{ interviewData }}</div>
    </div>
  </li>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {
    jobInfo: Object
  },
  computed: {
    interviewData() {
      return `${this.jobInfo.filter}/${this.jobInfo.interview}/${this.jobInfo.finish}`;
    },
    distribution() {
      return `${this.jobInfo.distributed}/${this.jobInfo.undistributed}`;
    }
  }
};
</script>

<style lang="scss" scoped>
.interview-manage-item {
  position: relative;
  display: inline-block;
  width: 30%;
  height: 230px;
  background: $primaryBlue;
  margin-top: 20px;
  .position {
    margin: 10px 0 0 10px;
    font-size: $mediumFS;
  }
  .data {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: $smallFS;
    margin-right: 10px;
    & > div {
      margin-bottom: 10px;
    }
  }
}
</style>
