<template>
  <div class="register">
    <register-header :title="title"></register-header>
    <el-card class="main-card">
      <register-step-tips></register-step-tips>
      <router-view></router-view>
    </el-card>
  </div>
</template>

<script>
import { RegisterHeader, RegisterStepTips } from "./components";

export default {
  name: "Register",
  components: {
    RegisterHeader,
    RegisterStepTips
  },
  computed: {
    title() {
      const titleMap = {
        enterprise: "企业注册",
        candidate: "求职者注册",
        "third-party": "第三方注册",
        success: "注册成功"
      };
      return titleMap[this.$route.name] || "注册";
    }
  }
};
</script>

<style lang="scss" scoped>
.main-card {
  width: $centerWidth;
  margin: ($headerHeight + 10px) auto;
}
</style>
