<template>
  <el-card class="job-details">
    <div slot="header" class="header">
      <span class="title">岗位详情</span>
    </div>
    <div class="main">
      <div class="job-info">
        <div class="job-info-header">
          <span class="position">{{ jobInfo.position }}</span>
          <span class="company">{{ jobInfo.company }} </span>
        </div>
        <ul class="job-info-main ul-list">
          <li class="ul-item" v-for="item of jobInfo.info" :key="item.id">
            {{ item.content }}
          </li>
        </ul>
      </div>
      <job-info-title-fill title="岗位亮点">
        <ul class="highlights-list ul-list">
          <li
            class="highlights-item ul-item"
            v-for="highlight of jobInfo.highlights"
            :key="highlight.id"
          >
            <el-tag>{{ highlight.content }}</el-tag>
          </li>
        </ul>
      </job-info-title-fill>
      <job-info-title-fill title="技能要求">
        <ul class="skills-list ul-list">
          <li
            class="skills-item ul-item"
            v-for="skill of jobInfo.skills"
            :key="skill.id"
          >
            <el-tag type="warning">{{ skill.content }}</el-tag>
          </li>
        </ul>
      </job-info-title-fill>
      <job-info-title-fill title="岗位职责">
        <div class="duty">{{ jobInfo.duty }}</div>
      </job-info-title-fill>
      <job-info-title-fill title="岗位要求">
        <div class="demand">{{ jobInfo.demand }}</div>
      </job-info-title-fill>
      <job-info-title-fill title="福利待遇">
        <div class="welfare">{{ jobInfo.welfare }}</div>
      </job-info-title-fill>
    </div>
    <div class="footer">
      <el-button v-if="type === 'candidate'" type="primary" @click="handleApply"
        >申请职位</el-button
      >
      <el-button v-else @click="handleReturn">返回</el-button>
    </div>
  </el-card>
</template>

<script>
import JobInfoTitleFill from "./JobInfoTitleFill";

export default {
  name: "",
  components: {
    JobInfoTitleFill
  },
  props: {
    type: {
      type: String,
      default: "candidate"
    }
  },
  data() {
    return {
      jobInfo: {
        position: "web前端工程师",
        company: "哗啦啦有限公司",
        info: [
          {
            id: 1,
            content: "10k-25k"
          },
          {
            id: 2,
            content: "深圳"
          },
          {
            id: 3,
            content: "经验不限"
          },
          {
            id: 4,
            content: "本科"
          },
          {
            id: 5,
            content: "招聘10人"
          }
        ],
        highlights: [
          {
            id: 1,
            content: "房补餐补"
          },
          {
            id: 2,
            content: "五险一金"
          },
          {
            id: 3,
            content: "带薪年假"
          }
        ],
        skills: [
          {
            id: 1,
            content: "html"
          },
          {
            id: 2,
            content: "css"
          },
          {
            id: 3,
            content: "javascript"
          }
        ],
        duty: `1、负责公司小程序、公众号、H5及内部系统前端开发工作；
2、负责web前端框架架构设计和技术难点公关；
3、负责web前端所需页面的制作和效果实现；
4、与UI设计师，产品、后台开发人员配合，根据需求调整，修改，优化页面；
5、根据原型高效实现前端页面，并对页面性能进行把控；
6、熟悉团队开发流程，有带团队经历优先（本岗位属于小组长岗位）。`,
        demand: `1、男女不限，本科及本科以上学历，计算机相关专业；
2、熟悉使用HTML+CSS+JS,对HTML/CSS/原生Javascript基础知识掌握一定要扎实；
3、熟练使用Vue,并且Vue项目经验2年以上；
4、有HTML5、CSS3页面编写经验；
5、熟悉JQuery框架等主流架构。`,
        welfare: `1、公司提供住宿，公寓式宿舍，环境舒适，有空调、热水器
2、入职后员工享有带薪休假、交付五险一金、买房补贴等
3、给予完善的绩效考核，年终奖金及定期调薪
4、转正后无责底薪+业绩提成+优秀奖=5K-8K
5、工作之余公司也会组织丰富的业余集体活`
      }
    };
  },
  methods: {
    handleApply() {
      this.$message.success("申请职位成功");
      this.$router.push("/candidate");
    },
    handleReturn() {
      this.$router.back(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.job-details {
  /deep/ .el-card__body {
    padding: 20px 40px;
  }
  .header {
    .title {
      @extend .font-title;
    }
  }
  .main {
    & > div {
      margin-bottom: 30px;
    }
    .job-info {
      &-header {
        @include flex(all, space-between);
        margin-bottom: 15px;
        .position {
          font-size: $largeFS;
          font-weight: 500;
        }
        .company {
          color: $primaryBlue;
          font-size: $mediumFS;
          font-weight: 500;
        }
      }
      &-main {
        li:first-child {
          color: $pRed;
          font-size: $largeFS;
        }
      }
    }
    .duty,
    .demand,
    .welfare {
      line-height: 22px;
      white-space: pre;
    }
  }
  .ul-item {
    display: inline-block;
    padding-right: 20px;
  }
  .footer {
    text-align: center;
    margin-bottom: 20px;
    button {
      width: 200px;
    }
  }
}
</style>
