<template>
  <div class="side-navigation">
    <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item
        v-for="item in navigationList"
        :key="item.id"
        :index="item.id.toString()"
        @click="handleClickRouter(item.router_link)"
      >
        <i :class="item.icon"></i>
        <span slot="title">{{ item.name }}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: "sideNavigation",
  components: {},
  props: {
    navigationList: Array,
    user: String
  },
  data() {
    return {
      userType: this.user
    };
  },
  methods: {
    handleClickRouter(routerName) {
      this.$router.push(`/${this.userType}/${routerName}`);
    }
  }
};
</script>

<style lang="scss" scoped>
.side-navigation {
  width: 100%;
  height: 100%;
  .el-menu-vertical-demo {
    height: 100%;
  }
}
</style>
