<template>
  <common-header
    class="candidate-header"
    :hasSearch="hasSearch"
    :titleContent="navContent"
  >
    <el-avatar :size="40" alt="头像" :src="avatarUrl" />
  </common-header>
</template>

<script>
import CommonHeader from "@/views/components/home/HomeHeader";

export default {
  name: "CandidateHeader",
  components: {
    CommonHeader
  },
  data() {
    return {
      avatarUrl: require("@/assets/images/pika.jpg"),
      hasSearch: false,
      navContent: [
        {
          id: 1,
          title: "首页",
          route: "/candidate"
        }
      ]
    };
  },
  watch: {
    $route: {
      handler(to) {
        if (to.path === "/candidate") {
          this.hasSearch = true;
        } else {
          this.hasSearch = false;
        }
      },
      immediate: true
    }
  }
};
</script>

<style lang="scss" scoped></style>
