<template>
  <el-select
    v-model="value"
    :placeholder="placeholder || '请选择'"
    :size="size"
  >
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "Select",
  components: {},
  props: {
    options: Array,
    placeholder: String,
    size: {
      type: String,
      default: "normal"
    },
    propValue: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      value: this.propValue
    };
  }
};
</script>

<style lang="scss" scoped></style>
