<template>
  <div class="hr-interview-filter">
    <interview-header>
      <div class="title">web前端开发—申报详情</div>
      <el-table
        class="table"
        ref="multipleTable"
        :data="tableData"
        style="width: 100%"
      >
        <el-table-column
          align="center"
          type="selection"
          width="55"
          :selectable="handleSelectTable"
        ></el-table-column>
        <el-table-column
          prop="state"
          label="状态"
          :width="80"
          align="center"
          :filters="[
            { text: '初筛', value: '初筛' },
            { text: '面试中', value: '面试中' },
            { text: '完成', value: '完成' }
          ]"
          :filter-method="filterState"
        >
        </el-table-column>
        <el-table-column
          v-for="column of columnList"
          :key="column.id"
          :prop="column.prop"
          :label="column.label"
          :width="column.width"
          align="center"
        >
        </el-table-column>
        <el-table-column align="center" prop="operate" label="操作">
          <template slot-scope="scope">
            <span
              class="view-resume"
              @click="handleClick(scope.$index, scope.row)"
              >查看简历</span
            >
          </template>
        </el-table-column>
      </el-table>
      <Pagination
        :hasCard="false"
        class="pagination"
        :total="100"
        :pageSize="10"
      />
      <div class="btn">
        <el-button type="primary" size="small" @chick="selectToInterviewing"
          >筛选至面试中</el-button
        >
        <el-button type="primary" size="small" @click="handleNext"
          >下一步</el-button
        >
        <el-button size="small" @click="handleBack">返回</el-button>
      </div>
    </interview-header>
  </div>
</template>

<script>
import InterviewHeader from "./components/HrInterviewHeader";
import { Pagination } from "@/components";

export default {
  name: "",
  components: {
    InterviewHeader,
    Pagination
  },
  data() {
    return {
      columnList: [
        {
          id: 2,
          prop: "name",
          label: "姓名",
          width: "80"
        },
        {
          id: 3,
          prop: "gender",
          label: "性别",
          width: "50"
        },
        {
          id: 4,
          prop: "email",
          label: "邮箱",
          width: "160"
        },
        {
          id: 5,
          prop: "phone",
          label: "手机号码",
          width: "120"
        },
        {
          id: 6,
          prop: "school",
          label: "毕业学校",
          width: "80"
        },
        {
          id: 7,
          prop: "education",
          label: "学历",
          width: "60"
        }
      ],
      tableData: [
        {
          state: "初筛",
          name: "李宝伟",
          gender: "男",
          email: "1549673926@qq.com",
          phone: "17817085209",
          school: "深圳大学",
          education: "硕士",
          operate: "查看简历"
        },
        {
          state: "面试中",
          name: "李宝伟",
          gender: "男",
          email: "1549673926@qq.com",
          phone: "17817085209",
          school: "深圳大学",
          education: "硕士",
          operate: "查看简历"
        }
      ]
    };
  },
  methods: {
    handleClick(index, row) {
      console.log(index, row);
      this.$router.push("/hr/resume-details");
    },
    filterState(value, row) {
      console.log(value, row);
      return row.state === value;
    },
    handleBack() {
      this.$router.back(-1);
    },
    handleNext() {
      this.$router.push("/hr/interviewing");
    },
    handleSelectTable(row) {
      if (row.state === "初筛") {
        return 1;
      } else {
        return 0;
      }
    },
    selectToInterviewing() {
      this.$message.success("筛选成功");
    }
  }
};
</script>

<style lang="scss" scoped>
.hr-interview-filter {
  .title {
    text-align: center;
    font-size: $largeFS;
    margin: 40px 0 20px;
  }
  /deep/ .el-table__row:nth-child(odd) {
    background: oldlace;
  }
  /deep/ .el-table__row:nth-child(even) {
    background: #f0f9eb;
  }
  .view-resume {
    color: $warningBrown;
    cursor: pointer;
    &:hover {
      color: $primaryBlue;
    }
  }
  .pagination {
    margin: 20px 0;
  }
  .btn {
    margin: 30px 0;
    text-align: center;
    button {
      margin: 0 20px;
    }
  }
}
</style>
