<template>
  <div class="third-party-info">
    <register-info-title-fill class="personal-info" title="个人信息">
      <register-info
        :infoList="personalInfoList"
        identity="third-party"
      ></register-info>
    </register-info-title-fill>
    <register-info-title-fill class="company-info" title="机构信息">
      <register-info
        :infoList="companyInfoList"
        registerBtn
        identity="third-party"
        @handle-register="handleRegister"
      ></register-info>
    </register-info-title-fill>
  </div>
</template>

<script>
import { RegisterInfoTitleFill, RegisterInfo } from "./components";
import * as localUtils from "./utils";

export default {
  name: "RegisterThirdParty",
  components: {
    RegisterInfoTitleFill,
    RegisterInfo
  },
  data() {
    return {
      width: "15%",
      gender: "0",
      identity: "0",
      personalInfoList: [
        {
          id: 1,
          type: "input",
          title: "姓名",
          class: "name",
          placeholder: "请填写您的真实姓名",
          model: "",
          keyName: "name"
        },
        {
          id: 2,
          type: "radio",
          title: "性别",
          class: "gender",
          model: 0,
          keyName: "gender",
          data: [
            {
              id: 1,
              label: 0,
              content: "男"
            },
            {
              id: 2,
              label: 1,
              content: "女"
            }
          ]
        },
        {
          id: 3,
          type: "input",
          title: "手机",
          class: "phone",
          placeholder: "请填写您的手机号码",
          model: "",
          keyName: "tel"
        },
        {
          id: 4,
          type: "input",
          title: "职位",
          class: "position",
          placeholder: "请填写您的职位(如:大学教授)",
          model: "",
          keyName: "position"
        }
      ],
      companyInfoList: [
        {
          id: 1,
          type: "input",
          title: "机构全称",
          class: "company-name",
          placeholder: "请填写您所在机构的全称(如:深圳大学)",
          model: "",
          keyName: "institute"
        },
        {
          id: 2,
          type: "input",
          title: "注册地",
          class: "company-address",
          placeholder: "请填写您机构的注册地点(如:深圳市南山区)",
          model: "",
          keyName: "location"
        }
      ],
      email: ""
    };
  },
  created() {
    this.email = localUtils.analyseUrlQueryEmail(this);
  },
  methods: {
    handleRegister() {
      const data = {
        roleId: 4,
        email: this.email
      };
      this.personalInfoList.forEach(item => {
        data[item.keyName] = item.model;
      });
      this.companyInfoList.forEach(item => {
        data[item.keyName] = item.model;
      });
      localUtils.addMoreInfo(data, this);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./components/mixin.scss";

.third-party-info {
  @extend .e-register-info;
  .company-info {
    margin-top: 40px;
  }
}
</style>
