<template>
  <div class="resume">
    <div class="resume-wrapper" @click.stop="handleClick($event)">
      <!-- attrs主要传递的是readOnly -->
      <resume-title-fill title="个人信息" type="edit" v-bind="$attrs">
        <resume-info class="info" :avatar="resumeInfo.avatar"></resume-info>
      </resume-title-fill>
      <resume-title-fill title="求职意向" type="edit" v-bind="$attrs">
        <resume-apply
          class="apply"
          :applyInfo="resumeInfo.applyInfo"
        ></resume-apply>
      </resume-title-fill>
      <resume-title-fill
        v-for="expItem of expList"
        :key="expItem.id"
        :title="expItem.title"
        type="add"
        v-bind="$attrs"
      >
        <resume-experience-list
          class="experience"
          :experienceList="expItem.experienceList"
          v-bind="$attrs"
        ></resume-experience-list>
      </resume-title-fill>
      <resume-title-fill title="自我评价" type="edit" v-bind="$attrs">
        <resume-self-evaluation
          class="evaluation"
          :selfEvaluation="resumeInfo.selfEvaluation"
        ></resume-self-evaluation>
      </resume-title-fill>
      <resume-title-fill title="附件" v-bind="$attrs">
        <resume-attachment
          class="attachment"
          v-bind="$attrs"
        ></resume-attachment>
      </resume-title-fill>
    </div>
    <Dialog
      top="5vh"
      :title="dialogTitle"
      width="60%"
      :dialogShow.sync="dialogShow"
    >
      <component :is="currentView" :edit="dialogEdit"></component>
    </Dialog>
  </div>
</template>

<script>
import ResumeInfo from "./ResumeInfo";
import ResumeApply from "./ResumeApply";
import ResumeExperienceList from "./ResumeExperienceList";
import ResumeSelfEvaluation from "./ResumeSelfEvaluation";
import ResumeAttachment from "./ResumeAttachment";
import ResumeTitleFill from "./ResumeTitleFill";
import Dialog from "./Dialog";
import ResumeInfoEdit from "./ResumeInfoEdit";
import ResumeApplyEdit from "./ResumeApplyEdit";
import ResumeWorkEdit from "./ResumeWorkEdit";
import ResumeProjectEdit from "./ResumeProjectEdit";
import ResumeEducationEdit from "./ResumeEducationEdit";
import ResumeEvaluationEdit from "./ResumeEvaluationEdit";

export default {
  name: "Resume",
  components: {
    ResumeInfo,
    ResumeApply,
    ResumeExperienceList,
    ResumeSelfEvaluation,
    ResumeAttachment,
    ResumeTitleFill,
    Dialog,
    ResumeInfoEdit,
    ResumeApplyEdit,
    ResumeWorkEdit,
    ResumeProjectEdit,
    ResumeEducationEdit,
    ResumeEvaluationEdit
  },
  data() {
    return {
      resumeInfo: {
        avatar: require("@/assets/images/pika.jpg"),
        name: "李宝伟",
        updateTime: "2019-12-25",
        gender: "男",
        age: 18,
        experience: "无工作经验",
        phone: "17817085209",
        email: "1549673926@qq.com",
        applyInfo: {
          applyState: "应届毕业生",
          expectIndustry:
            "计算机软件计算机软件计算机软件计算机软件计算机软件计算机软件",
          expectJob: "web前端web前端web前端web前端",
          expectMonthSalary: 20000
        },
        workExperienceList: [
          {
            id: 1,
            company: "哗啦啦有限公司",
            position: "Web前端高级工程师",
            description:
              "1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合",
            startTime: "2019.01.01",
            endTime: "2020.01.01"
          },
          {
            id: 2,
            company: "哗啦啦有限公司",
            position: "Web前端高级工程师",
            description:
              "1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合1.负责公司前端开发2.与UI设计师、产品与后台人员配合",
            startTime: "2019.01.01",
            endTime: "2020.01.01"
          }
        ],
        projectExperienceList: [
          {
            id: 1,
            name: "博客系统",
            description: "1.负责公司前端开发2.与UI设计师、产品与后台人员配合",
            startTime: "2019-01-01",
            endTime: "2020-01-01"
          },
          {
            id: 2,
            name: "博客系统1",
            description: "1.负责公司前端开发2.与UI设计师、产品与后台人员配合",
            startTime: "2019-01-01",
            endTime: "2020-01-01"
          }
        ],
        educationList: [
          {
            id: 1,
            school: "深圳大学",
            specialty: "信息与计算科学",
            educationalBack: "硕士",
            startTime: "2016-09-01",
            endTime: "2020-07-01"
          },
          {
            id: 2,
            school: "深圳大学",
            specialty: "信息与计算科学",
            educationalBack: "硕士",
            startTime: "2016-09-01",
            endTime: "2020-07-01"
          }
        ],
        selfEvaluation:
          "1.对前端有浓烈兴趣：空闲时喜欢仿写一些网页。2.对数据敏感、有团队协作沟通意识：具有多次组队建模经验",
        attachment: "李宝伟的简历.pdf"
      },
      dialogShow: false,
      dialogTitle: "",
      dialogEdit: true
    };
  },
  methods: {
    handleClick(e) {
      // 事件委托
      const target = e.target;
      // 防御编程
      if (!target.tagName) {
        return;
      }
      const tagName = target.tagName.toLowerCase();
      const classList = target.classList;
      const parent = target.parentElement;
      if (
        (tagName === "i" && classList.contains("edit")) ||
        (tagName === "button" && classList.contains("add"))
      ) {
        this.dialogTitle = target.dataset.title;
        this.dialogShow = true;
        this.dialogEdit = true;
      } else if (
        parent.tagName.toLowerCase() === "button" &&
        parent.classList.contains("add")
      ) {
        this.dialogTitle = parent.dataset.title;
        this.dialogShow = true;
        this.dialogEdit = false;
      }
    }
  },
  computed: {
    expList() {
      return [
        {
          id: 1,
          title: "工作经验",
          experienceList: this.resumeInfo.workExperienceList,
          edit: false
        },
        {
          id: 2,
          title: "项目经验",
          experienceList: this.resumeInfo.projectExperienceList,
          edit: false
        },
        {
          id: 3,
          title: "教育经历",
          experienceList: this.resumeInfo.educationList,
          edit: false
        }
      ];
    },
    currentView() {
      const titleCurrentViewMap = {
        个人信息: "resume-info-edit",
        求职意向: "resume-apply-edit",
        工作经验: "resume-work-edit",
        项目经验: "resume-project-edit",
        教育经历: "resume-education-edit",
        自我评价: "resume-evaluation-edit"
      };
      return titleCurrentViewMap[this.dialogTitle];
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./resume.scss";

.resume {
  // padding: 0 5%;
  &-wrapper {
    $padding-left: 20px;
    & > div {
      padding-bottom: 15px;
      margin: 0 20px 30px 0;
      border-bottom: $secondBorder;
    }
    .info,
    .apply,
    .evaluation,
    .attachment {
      padding: 15px 0 15px $padding-left;
    }
    .experience {
      padding-left: $padding-left;
    }
  }
}
</style>
