<template>
  <div class="interview-record">
    <div class="search-container">
      <slot name="search"></slot>
    </div>
    <div
      class="item-container"
      v-for="item in interviewRecordList"
      :key="item.intervId"
    >
      <div v-if="type != candidate">{{ item.name }}</div>
      <div v-if="type != interviewer">{{ item.company }}</div>
      <div>{{ item.intervTime }}</div>
      <div class="progress">{{ item.intervProg }}</div>
      <div class="btn">
        <el-button
          size="mini"
          type="primary"
          @click="handleClick(item.intervId)"
          >视频记录</el-button
        >
        <div class="score-record" v-if="type == interviewer">
          <el-button
            size="mini"
            type="primary"
            @click="handleClick(item.intervId)"
            >评分记录</el-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "interviewRecord",
  components: {},
  props: {
    interviewRecordList: Array,
    type: String
  },
  data() {
    return {
      thirdParty: "third-party",
      candidate: "candidate",
      interviewer: "interviewer",
      userType: this.type
    };
  },
  methods: {
    handleClick() {
      this.$router.push(`${this.type}/video-record`);
    }
  }
};
</script>

<style lang="scss" scoped>
.interview-record {
  width: 100%;
  background: $white;
  padding: 10px;
  box-sizing: border-box;

  .search-container {
    width: 100%;
    @include flex(justify-content, flex-end);
  }
  .item-container {
    display: flex;
    height: 45px;
    & > div {
      flex: 1;
      @include flex(all, center, center);
      color: $normalFC;
    }
    .progress {
      color: $dangerRed;
    }
    .btn {
      flex: 1.5;
      justify-content: flex-end;
      .score-record {
        margin-left: 10px;
      }
    }
  }
}
</style>
