import { get, post, put, del } from "./http";

// 登录部分
export const login = data => post("/login", data);

// 注册部分
export const verifyEmail = data => post("/register/email_verify", data);
export const addMoreInfo = data => post("/register/more_info", data);
export const verifyPwd = data => post("/register/pwd_verify", data);

// 获取用户信息
export const getUserInfo = () => post("/user/findByEmail");

// hr部分
export const getPosition = data => get("/hr/position/findByKeyword", data);
export const publishPosition = data => post(`/hr/position/${data.id}`, data);
export const delPosition = data => del(`/hr/position/${data.id}`, data);
export const updatePosition = data => put(`/hr/position/${data.id}`, data);
