export const addMoreInfo = (data, ctx) => {
  ctx.$axios
    .addMoreInfo(data)
    .then(res => {
      console.log(res);
      ctx.$message.success("信息完善完成");
      ctx.$router.push("/register/success");
    })
    .catch(err => {
      console.log(err);
      if (err === "操作超时") {
        ctx.$message.warning("操作超时，请保证网络连接完好，再重新操作！");
      } else {
        ctx.$message.warning("注册失败，请检查信息是否填写完整");
      }
    });
};

export const analyseUrlQueryEmail = ctx => {
  return ctx.$route.query.email.replace("%40", "@");
};
