<template>
  <center-main class="candidate-center" @handleSelect="handleSelect">
    <component class="view" :is="currentView"></component>
  </center-main>
</template>

<script>
import CenterMain from "./components/CenterMain";
import Resume from "@/views/components/resume/Resume";
import CandidateDeliverRecord from "./components/CandidateDeliverRecord";
import CandidateInterviewRecord from "./components/CandidateInterviewRecord";
import PersonDataEdit from "../components/PersonDataEdit";
import CommonSetting from "../components/CommonSetting";

export default {
  name: "CandidateCenter",
  components: {
    CenterMain,
    Resume,
    CandidateDeliverRecord,
    CandidateInterviewRecord,
    PersonDataEdit,
    CommonSetting
  },
  data() {
    return {
      currentView: "resume"
    };
  },
  methods: {
    handleSelect(key) {
      const keyMap = [
        "resume",
        "candidate-deliver-record",
        "candidate-interview-record",
        "person-data-edit",
        "common-setting"
      ];
      this.currentView = keyMap[Number(key) - 1];
    }
  }
};
</script>

<style lang="scss" scoped>
.candidate-center {
  .view {
    margin: 40px 0;
    padding: 0 5%;
  }
}
</style>
