<template>
  <div class="register-step-tips">
    <span class="main" v-for="(item, index) of titleList" :key="item.id">
      <router-link v-if="route === true" :to="item.route">
        <span :class="['step', { current: currentStep(index) }]">
          {{ item.title }}
        </span>
      </router-link>
      <span v-else :class="['step', { current: currentStep(index) }]">
        {{ item.title }}
      </span>
      <span v-if="index !== titleList.length - 1" class="line">————</span>
    </span>
  </div>
</template>

<script>
export default {
  name: "StepTips",
  components: {},
  props: {
    titleList: Array,
    route: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      current: 0
    };
  },
  methods: {
    next() {
      if (this.current++ >= this.titleList.length - 1) {
        this.current = 0;
      }
    }
  },
  created() {
    this.titleList.forEach((item, index) => {
      if (item.route === this.$route.path) {
        this.current = index;
      }
    });
  },
  computed: {
    currentStep() {
      return function(index) {
        return this.current === index;
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.register-step-tips {
  @include flex();
  .main > span {
    margin-right: 15px;
  }
  .current {
    font-size: $mediumFS;
    color: $primaryBlue;
  }
}
</style>
