<template>
  <div class="question-bank">
    <el-card class="left">
      <span slot="header" class="title">
        题库管理
      </span>
      <div class="select-button-container">
        <Select
          :options="options"
          placeholder="请选择标签"
          size="small"
          class="tag-select"
        ></Select>
        <el-button
          class="insert-question-btn"
          type="primary"
          @click="insertQuestion"
          size="small"
          >{{ differentFeatureButton }}</el-button
        >
      </div>
      <question-common-table
        :tableData="tableData"
        :hasSelect="hasSelect"
        @clickTableRowId="handleClickTableRowId"
      ></question-common-table>
      <Pagination
        class="pagination"
        :hasCard="false"
        :total="100"
        :pageSize="10"
      ></Pagination>
    </el-card>
    <div class="right">
      <el-card class="right-card">
        <question-look-more-container class="tag-container" :title="'标签分类'">
          <question-common-tag
            :TagItems="totalTag"
            slot="content"
          ></question-common-tag>
        </question-look-more-container>
        <question-look-more-container
          class="question-description"
          :title="'题目说明'"
        >
          <div slot="content">
            <p
              class="detail"
              v-for="item in questionDescription"
              :key="item.id"
            >
              {{ item.content }}
            </p>
          </div>
        </question-look-more-container>
      </el-card>
    </div>
    <Dialog
      top="5vh"
      :title="dialogName"
      width="60%"
      :dialogShow.sync="dialogShow"
      :buttonFeature="buttonFeature"
    >
      <div class="title-fill-container">
        <title-fill class="related-tag" title="相关标签" :width="width">
          <question-common-tag
            :TagItems="tagItems"
            :readOnly="readOnly"
            :totalTag="totalTag"
          ></question-common-tag>
        </title-fill>

        <title-fill
          class="question-description"
          title="题目描述"
          :width="width"
        >
          <el-input
            type="textarea"
            rows="10"
            placeholder="必填，不能超过1000个字符"
            :maxlength="1000"
            v-model="textAreaContent"
            clearable
            show-word-limit
            :readonly="readOnly"
          ></el-input>
        </title-fill>
      </div>
    </Dialog>
  </div>
</template>

<script>
import {
  QuestionCommonTable,
  QuestionCommonTag,
  QuestionLookMoreContainer
} from "@/views/components/question-bank/index";
import { Pagination, Select } from "@/components/index";
import Dialog from "@/views/components/resume/Dialog";
import { TitleFill } from "@/components";

export default {
  name: "questionBank",
  props: {
    hasSelect: {
      type: Boolean,
      default: false
    }
  },
  components: {
    QuestionCommonTable,
    Pagination,
    QuestionCommonTag,
    QuestionLookMoreContainer,
    Dialog,
    TitleFill,
    Select
  },
  data() {
    return {
      dialogName: "题目详情",
      readOnly: true,
      description: "",
      textAreaContent: "",
      buttonFeature: "删除",
      width: "15%",
      dialogShow: false,
      headerTitle: "题库管理",
      tagItems: [],
      tableData: [
        {
          id: 1,
          questionName: "aaaaaaa",
          starGrade: "5星",
          average: "90",
          frequency: "10%"
        },
        {
          id: 2,
          questionName: "aaaaaaa",
          starGrade: "5星",
          average: "90",
          frequency: "10%"
        },
        {
          id: 3,
          questionName: "aaaaaaa",
          starGrade: "5星",
          average: "90",
          frequency: "10%"
        },
        {
          id: 4,
          questionName: "aaaaaaa",
          starGrade: "5星",
          average: "90",
          frequency: "10%"
        }
      ],

      totalTag: [
        {
          id: 1,
          label: "java"
        },
        {
          id: 2,
          label: "web"
        },
        {
          id: 3,
          label: "算法"
        },
        {
          id: 4,
          label: "c++"
        },
        {
          id: 5,
          label: "计算机网络"
        },
        {
          id: 6,
          label: "数据结构"
        },
        {
          id: 7,
          label: "11111"
        },
        {
          id: 8,
          label: "1111"
        },
        {
          id: 9,
          label: "11"
        },
        {
          id: 10,
          label: "1"
        }
      ],
      questionTagItemsData: [
        {
          id: 1,
          label: "java"
        },
        {
          id: 2,
          label: "web"
        },
        {
          id: 3,
          label: "算法"
        },
        {
          id: 4,
          label: "c++"
        }
      ],
      questionDetailDescription:
        "asjhdjaskhdasjkaksdsaksakdsakdsakldsakdsakldhsakdsal",
      questionDescription: [
        {
          id: 1,
          content:
            "星级：指的是所有面试官对该题目的价值评分，满星是5星，最低是1星"
        },
        {
          id: 2,
          content: "平均分：指的是该题在所有求职者答题分数的平均"
        },
        {
          id: 3,
          content: "出现频率：指的是该题在所有求职者答题分数的平均"
        }
      ],
      options: [
        {
          value: "选项1",
          label: "java"
        },
        {
          value: "选项2",
          label: "web"
        },
        {
          value: "选项3",
          label: "算法"
        },
        {
          value: "选项4",
          label: "网络"
        },
        {
          value: "选项5",
          label: "数据结构"
        }
      ]
    };
  },
  computed: {
    differentFeatureButton() {
      return this.hasSelect ? "生成试卷" : "录入题目";
    }
  },
  methods: {
    handleClickTableRowId(id) {
      console.log(id);
      this.handleDataChange("detail");
    },
    insertQuestion() {
      this.description = "";
      if (this.differentFeatureButton !== "生成试卷") {
        this.handleDataChange("insert");
      }
    },
    handleDataChange(type) {
      let bool = type === "insert";
      this.dialogShow = true;
      this.readOnly = bool ? false : true;
      this.buttonFeature = bool ? "录入" : "删除";
      this.dialogName = bool ? "题目录入" : "题目详情";
      this.tagItems = bool ? [] : this.questionTagItemsData;
      this.textAreaContent = bool
        ? this.description
        : this.questionDetailDescription;
    }
  }
};
</script>

<style lang="scss" scoped>
.question-bank {
  $right-inner-width: 85%;
  height: 100%;
  @include flex(justify-content, space-between);

  .left {
    height: 100%;
    width: 69%;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    .title {
      @extend .font-title-1;
    }
    .select-button-container {
      @include flex(all, space-between, center);
      margin: 10px 0 30px;
      button {
        width: 120px;
        border-radius: $largeBR;
      }
    }
    .pagination {
      margin: 10px 0;
    }
  }
  .right {
    height: 100%;
    width: 30%;
    box-sizing: border-box;
    .right-card {
      height: 100%;
      /deep/ .el-card__body {
        padding: 0px;
      }
      .tag-container {
        width: $right-inner-width;
        margin: 0 auto;
      }
      .question-description {
        width: $right-inner-width;
        margin: 30px auto;
        .detail {
          margin-top: 5px;
          line-height: 20px;
          @extend .font-title-3;
        }
      }
    }
  }
  .title-fill-container {
    width: 90%;
    .question-description {
      margin-top: 30px;
    }
  }
}
</style>
