<template>
  <header :class="{ 'home-header': true, 'is-special': isSpecial }">
    <div class="left">
      <img class="logo" src="@/assets/images/logo.svg" alt="" />
      <el-menu
        :default-active="defaultActive"
        class="el-menu-demo"
        mode="horizontal"
        :router="true"
      >
        <el-menu-item
          v-for="item in titleContent"
          class="menu-item"
          :index="item.id.toString()"
          :key="item.id"
          :route="{ path: item.route }"
        >
          {{ item.title }}
        </el-menu-item>
      </el-menu>
    </div>
    <div class="search" v-if="hasSearch">
      <el-input placeholder="请输入职位" size="small" />
      <el-button type="primary" size="small">职位搜索</el-button>
    </div>
    <div class="right">
      <slot></slot>
    </div>
  </header>
</template>

<script>
export default {
  name: "HomeHeader",
  props: {
    hasSearch: {
      type: Boolean,
      default: false
    },
    isSpecial: {
      type: Boolean,
      default: false
    },
    titleContent: {
      type: Array,
      default: () => [{ id: 1, title: "首页", route: "" }]
    },
    active: {
      type: String,
      default: ""
    }
  },
  components: {},
  computed: {
    defaultActive() {
      return (
        this.active ||
        (this.titleContent.length >= 1 && this.titleContent[0].id + "")
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.home-header {
  $left-width: 30%;
  @extend .home-header-style;
  @include flex(all, space-between);
  .left {
    @include flex(align-items);
    min-width: $left-width;
    .el-menu-demo {
      margin-left: 10px;
      border-bottom: none;
      .menu-item {
        @include line-height(40px);
        padding: 0;
        margin: 0 10px;
        @extend .font-title;
        font-size: 18px;
      }
    }
  }
  .search {
    width: 100% - 2 * $left-width - 5%;
    @include flex(align-items);
    .el-input {
      /deep/ input {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }
    .el-button {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
  .right {
    @include flex(all, flex-end, center);
    width: $left-width;
  }
}
.is-special {
  background: $navigationGray;
  .el-menu-demo {
    background: $navigationGray;
    .menu-item,
    .menu-item:hover,
    .is-active {
      background: $navigationGray;
    }
    .menu-item {
      color: $white !important;
    }
  }

  .right {
    color: $white;
  }
}
</style>
