<template>
  <div
    class="resume-experience-item"
    @mouseenter="dateShow = false"
    @mouseleave="dateShow = true"
  >
    <div v-if="experience.school" class="education">
      <span class="left">
        <span class="school">{{ experience.school }}</span>
        <span class="specialty">{{ experience.specialty }}</span>
        <span class="education-back">{{ experience.educationalBack }}</span>
      </span>
      <!-- itemType简历信息中的卡片类型 -->
      <resume-date
        :experience="experience"
        :itemType="itemType"
        :dateShow="show"
      ></resume-date>
    </div>
    <div v-else class="experience">
      <div class="header">
        <span class="title">{{ experience.company || experience.name }}</span>
        <resume-date
          :experience="experience"
          :itemType="itemType"
          :dateShow="show"
        ></resume-date>
      </div>
      <div class="position" v-if="positionShow">{{ experience.position }}</div>
      <div class="description">{{ experience.description }}</div>
    </div>
  </div>
</template>

<script>
import ResumeDate from "./ResumeDate";

export default {
  name: "ResumeExperienceItem",
  components: {
    ResumeDate
  },
  props: {
    experience: Object
  },
  data() {
    return {
      dateShow: true
    };
  },
  computed: {
    date() {
      return `${this.experience.startTime}—${this.experience.endTime}`;
    },
    positionShow() {
      return !!this.experience.position;
    },
    show() {
      return this.$attrs.readOnly || this.dateShow;
    },
    itemType() {
      if (this.experience.school) {
        return "education";
      } else if (this.experience.company) {
        return "work";
      } else {
        return "project";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./resume.scss";

.resume-experience-item {
  .education {
    @include flex(all, space-between);
    .left {
      @include flex(align-items);
      & > span {
        padding-right: 30px;
      }
      .school {
        @extend .font-title-2;
      }
    }
    .right {
      @extend .extend-resume-item-date;
    }
  }
  .experience {
    .header {
      @include flex(all, space-between, center);
      .title {
        @extend .font-title-2;
      }
    }
    & > div:not(:first-child) {
      margin-top: 15px;
    }
    .description {
      line-height: 22px;
    }
  }
}
</style>
