<template>
  <div class="resume-date">
    <span v-if="dateShow" class="date">{{ date }}</span>
    <span v-else>
      <i class="delete el-icon-delete"></i>
      <i :data-title="currentTitle" class="edit el-icon-edit-outline"></i>
    </span>
  </div>
</template>

<script>
export default {
  name: "ResumeDate",
  components: {},
  props: {
    experience: Object,
    dateShow: Boolean,
    itemType: String
  },
  data() {
    return {
      dialogShow: false
    };
  },
  computed: {
    date() {
      return `${this.experience.startTime}—${this.experience.endTime}`;
    },
    currentTitle() {
      const currentTitleMap = {
        education: "教育经历",
        work: "工作经验",
        project: "项目经验"
      };
      return currentTitleMap[this.itemType];
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./resume.scss";

.resume-date {
  .date {
    color: $secondaryFC;
    font-size: $smallFS;
  }
  .edit,
  .delete {
    @extend .extend-resume-item-edit;
  }
  .edit {
    margin-left: 30px;
  }
}
</style>
