<template>
  <div ref="outer" class="question-look-more-container">
    <div class="title">
      {{ title }}
    </div>
    <div ref="inner" class="content-container">
      <slot name="content"></slot>
      <div class="look-more" v-if="buttonShow" @click="handleClick()">
        {{ text }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "questionLookMoreContainer",
  components: {},
  props: {
    title: String
  },
  data() {
    return {
      buttonShow: true,
      titleHeight: 50,
      text: "查看更多...",
      isPackUp: true,
      innerHeight: 0,
      outerHeight: 0
    };
  },
  mounted() {
    this.innerHeight = this.$refs.inner.offsetHeight;
    this.outerHeight = this.$refs.outer.offsetHeight;
    this.buttonShow = this.outerHeight - this.titleHeight < this.innerHeight;
  },
  methods: {
    handleClick() {
      this.isPackUp = !this.isPackUp;
      this.text = this.isPackUp ? "查看更多..." : "收起";
      this.$refs.outer.style.height = this.isPackUp
        ? this.outerHeight + "px"
        : this.innerHeight + this.titleHeight + 20 + "px";
    }
  }
};
</script>

<style lang="scss" scoped>
.question-look-more-container {
  width: 100%;
  height: 200px;
  padding: 10px;
  box-sizing: border-box;
  overflow: hidden;
  background: $white;
  position: relative;

  .title {
    @include line-height(30px);
    @extend .font-title-2;
    border-bottom: $fourthBorder;
    margin-bottom: 10px;
  }
  .look-more {
    @include line-height(20px);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    @extend .font-title-3;
    color: lightblue;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0), #fff);
    cursor: pointer;
  }
}
</style>
