import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/home/Home";
import ThirdParty from "@/views/third-party/ThirdParty";
import ThirdPartyHome from "@/views/third-party/components/ThirdPartyHome";
import VideoRecord from "@/views/components/video-record/VideoRecord";
import ComponentTest from "@/views/test/ComponentTest";
import {
  RegisterCandidate,
  RegisterEnterprise,
  RegisterThirdParty,
  RegisterSuccess,
  Register,
  RegisterMain
} from "@/views/register";
import Candidate from "@/views/candidate/Candidate";
import CandidateHome from "@/views/candidate/CandidateHome";
import CandidateCenter from "@/views/candidate/CandidateCenter";
import CandidateDetails from "@/views/candidate/CandidateDetails";
import Interviewer from "@/views/interviewer/Interviewer";
import {
  InterviewerManagement,
  InterviewerQuestion,
  InterviewerPersonalCenter,
  InterviewerRecord
} from "@/views/interviewer/components";
import Hr from "@/views/hr/Hr";
import HrHome from "@/views/hr/HrHome";
import HrJobPublish from "@/views/hr/HrJobPublish";
import HrJobEdit from "@/views/hr/HrJobEdit";
import HrResumeManage from "@/views/hr/HrResumeManage";
import HrResumeDetails from "@/views/hr/HrResumeDetails";
import HrJobDetails from "@/views/hr/HrJobDetails";
import HrInterviewManage from "@/views/hr/HrInterviewManage";
import HrInterviewFilter from "@/views/hr/HrInterviewFilter";
import HrInterviewing from "@/views/hr/HrInterviewing";
import HrInterviewFinish from "@/views/hr/HrInterviewFinish";
import HrCenter from "@/views/hr/HrCenter";
import HrQuestionBank from "@/views/hr/HrQuestionBank";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/test",
    name: "componentTest",
    component: ComponentTest
  },
  {
    path: "/register",
    component: Register,
    children: [
      {
        path: "",
        name: "register",
        component: RegisterMain
      },
      {
        path: "candidate",
        name: "register-candidate",
        component: RegisterCandidate
      },
      {
        path: "enterprise",
        name: "register-enterprise",
        component: RegisterEnterprise
      },
      {
        path: "third-party",
        name: "register-third-party",
        component: RegisterThirdParty
      },
      {
        path: "success",
        name: "success",
        component: RegisterSuccess
      }
    ]
  },
  {
    path: "/candidate",
    component: Candidate,
    children: [
      {
        path: "",
        name: "candidate-home",
        component: CandidateHome
      },
      {
        path: "center",
        name: "candidate-center",
        component: CandidateCenter
      },
      {
        path: "details",
        name: "candidate-details",
        component: CandidateDetails
      }
    ]
  },
  {
    path: "/third-party",
    name: "",
    component: ThirdParty,
    children: [
      {
        path: "",
        name: "third-party-home",
        component: ThirdPartyHome
      },
      {
        path: "video-record",
        name: "video-record",
        component: VideoRecord
      }
    ]
  },
  {
    path: "/interviewer",
    component: Interviewer,
    children: [
      {
        path: "",
        name: "interviewer",
        component: InterviewerManagement
      },
      {
        path: "question",
        name: "question",
        component: InterviewerQuestion
      },
      {
        path: "personal-center",
        name: "personal-center",
        component: InterviewerPersonalCenter
      },
      {
        path: "record",
        name: "record",
        component: InterviewerRecord
      }
    ]
  },
  {
    path: "/hr",
    component: Hr,
    children: [
      {
        path: "",
        name: "hr-home",
        component: HrHome
      },
      {
        path: "publish",
        name: "hr-publish",
        component: HrJobPublish
      },
      {
        path: "edit",
        name: "hr-edit",
        component: HrJobEdit
      },
      {
        path: "resume-details",
        name: "hr-resume-details",
        component: HrResumeDetails
      },
      {
        path: "resume",
        name: "hr-resume",
        component: HrResumeManage
      },
      {
        path: "details",
        name: "hr-job-details",
        component: HrJobDetails
      },
      {
        path: "manage",
        name: "hr-interview-manage",
        component: HrInterviewManage
      },

      {
        path: "filter",
        name: "hr-interview-filter",
        component: HrInterviewFilter
      },
      {
        path: "interviewing",
        name: "hr-interviewing",
        component: HrInterviewing
      },
      {
        path: "finish",
        name: "hr-interview-finish",
        component: HrInterviewFinish
      },
      {
        path: "question",
        name: "hr-question",
        component: HrQuestionBank
      },
      {
        path: "center",
        name: "hr-center",
        component: HrCenter
      }
    ]
  }
];
// {
//   path: "/about",
//   name: "about",
//   // route level code-splitting
//   // this generates a separate chunk (about.[hash].js) for this route
//   // which is lazy-loaded when the route is visited.
//   component: () =>
//     import(/* webpackChunkName: "about" */ "../views/About.vue")
// }

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
