<template>
  <el-card class="hr-center">
    <el-menu
      class="header"
      @select="handleSelect"
      default-active="1"
      mode="horizontal"
    >
      <el-menu-item index="1">编辑资料</el-menu-item>
      <el-menu-item index="2">设置</el-menu-item>
    </el-menu>
    <component :is="currentView" class="view" propWidth="10%"></component>
  </el-card>
</template>

<script>
import PersonDataEdit from "../components/PersonDataEdit";
import CommonSetting from "../components/CommonSetting";

export default {
  name: "",
  components: {
    PersonDataEdit,
    CommonSetting
  },
  data() {
    return {
      currentView: "person-data-edit"
    };
  },
  methods: {
    handleSelect(key) {
      const keyMap = ["person-data-edit", "common-setting"];
      this.currentView = keyMap[Number(key) - 1];
    }
  }
};
</script>

<style lang="scss" scoped>
.hr-center {
  .view {
    margin: 30px 0 20px;
    padding: 0 10%;
  }
}
</style>
