<template>
  <el-dialog
    class="resume-dialog"
    :title="title"
    :visible.sync="dialogShow"
    width="60%"
    :before-close="closeDialog"
    :top="top"
  >
    <slot></slot>
    <span slot="footer" class="dialog-footer">
      <el-button
        class="save-btn"
        type="primary"
        size="small"
        @click="closeDialog"
        >{{ buttonFeature }}</el-button
      >
      <el-button size="small" @click="closeDialog">取消</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: "Dialog",
  components: {},
  props: {
    dialogShow: Boolean,
    title: String,
    width: String,
    top: String,
    buttonFeature: {
      type: String,
      default: "保存"
    }
  },
  data() {
    return {};
  },
  methods: {
    closeDialog() {
      this.$emit("update:dialogShow", false);
    }
  }
};
</script>

<style lang="scss" scoped>
.resume-dialog {
  /deep/ .el-dialog__footer {
    text-align: center;
  }
  .dialog-footer {
    button {
      width: 80px;
      margin: 0 20px;
    }
  }
}
</style>
