<template>
  <el-card class="job-publish-or-edit">
    <div slot="header" class="clearfix">
      <span class="title">岗位发布</span>
    </div>
    <div class="main">
      <title-fill title="岗位名称" :width="width">
        <el-input v-model="info.name"></el-input>
      </title-fill>
      <title-fill title="岗位职责" :width="width">
        <el-input v-model="info.duty" type="textarea" rows="6"></el-input>
      </title-fill>
      <title-fill title="岗位要求" :width="width">
        <el-input
          v-model="info.jobDemand"
          class="demand"
          type="textarea"
          rows="6"
        ></el-input>
      </title-fill>
      <title-fill title="技能要求" :width="width">
        <new-tag :tags="info.skills"></new-tag>
      </title-fill>
      <div class="demand">
        <title-fill title="学历要求" :width="width">
          <el-input v-model="info.education"></el-input>
        </title-fill>
        <title-fill title="经验要求" :width="width">
          <el-input v-model="info.experience"></el-input>
        </title-fill>
      </div>
      <div class="address-people">
        <title-fill title="工作地点" :width="width">
          <el-input v-model="info.location"></el-input>
        </title-fill>
        <title-fill title="招聘人数" :width="width">
          <el-input v-model="info.num"></el-input>
        </title-fill>
      </div>
      <div class="scale-salary">
        <title-fill title="公司规模" :width="width">
          <el-input v-model="info.scale"></el-input>
        </title-fill>
        <title-fill title="税前薪资" :width="width">
          <el-input v-model="info.salary"></el-input>
        </title-fill>
      </div>
      <title-fill title="截止时间" :width="width">
        <el-date-picker
          v-model="info.date"
          :default-value="info.date"
          type="date"
          placeholder="选择截止日期"
        >
        </el-date-picker>
      </title-fill>
      <title-fill title="岗位亮点" :width="width">
        <new-tag :tags="info.highlights"></new-tag>
      </title-fill>
      <title-fill title="福利待遇" :width="width">
        <el-input v-model="info.welfare" type="textarea" rows="6"></el-input>
      </title-fill>
    </div>
    <div class="btn">
      <el-button type="primary" size="small" @click="handlePublish">{{
        btnContent
      }}</el-button>
      <router-link to="/hr">
        <el-button size="small">返回</el-button>
      </router-link>
    </div>
  </el-card>
</template>

<script>
import { TitleFill, NewTag } from "@/components";

export default {
  name: "HrJobPublish",
  components: {
    TitleFill,
    NewTag
  },
  props: {
    pageType: {
      type: String,
      default: "publish" // 还有一个是edit默认是publish
    },
    jobInfo: {
      type: Object,
      default: () => ({
        name: "",
        duty: "",
        skills: [],
        jobDemand: "",
        education: "",
        experience: "",
        location: "",
        num: "",
        scale: "",
        salary: "",
        date: new Date(),
        highlights: [],
        welfare: ""
      })
    }
  },
  data() {
    return {
      width: "80px",
      date: "",
      info: { ...this.jobInfo }
    };
  },
  methods: {
    handlePublish() {
      this.$message.success("岗位发布成功");
      this.$router.push("/hr");
    }
  },
  computed: {
    btnContent() {
      const btnMap = {
        publish: "发布",
        edit: "编辑"
      };
      return btnMap[this.pageType];
    }
  }
};
</script>

<style lang="scss" scoped>
.job-publish-or-edit {
  .clearfix {
    @extend .font-title-1;
  }
  .el-date-editor {
    width: 50%;
  }
  .main {
    & > div:first-child {
      margin-top: 10px;
    }
    & > div {
      width: 90%;
      margin-bottom: 30px;
    }
    .demand,
    .address-people,
    .scale-salary {
      @include flex(all, space-between);
      & > div {
        width: 48%;
      }
    }
  }
  .btn {
    text-align: center;
    button {
      width: 80px;
      margin: 0 20px 30px;
    }
  }
}
</style>
