<template>
  <el-dialog
    class="home-login-dialog"
    title="登录"
    :visible.sync="loginDisplay"
    width="30%"
    center
    :before-close="handleDisplayChange"
  >
    <title-fill class="email" title="邮箱" width="10%">
      <el-input
        class="input"
        v-model="email"
        placeholder="请输入邮箱"
        clearable
      />
    </title-fill>
    <title-fill class="pwd" title="密码" width="10%">
      <el-input
        class="input"
        v-model="pwd"
        placeholder="请输入密码"
        show-password
      />
    </title-fill>
    <div class="login">
      <el-button type="primary" size="small" @click="handleLogin"
        >登录</el-button
      >
    </div>
    <div class="register">
      <el-button size="small" @click="handleRegister">注册</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { TitleFill } from "@/components";

export default {
  name: "HomeLoginDialog",
  components: {
    TitleFill
  },
  props: {
    loginDisplay: Boolean
  },
  data() {
    return {
      // loginDialogVisible: false,
      email: "",
      pwd: ""
    };
  },
  methods: {
    handleDisplayChange() {
      this.$emit("update:loginDisplay");
    },
    handleRegister() {
      this.handleDisplayChange();
      this.$router.push("/register");
    },
    handleLogin() {
      const data = {
        email: this.email,
        pwd: this.pwd
      };
      this.$axios
        .login(data)
        .then(res => {
          let roleMap = ["hr", "candidate", "interviewer", "third-party"];
          this.$message.success("登录成功");
          this.$store.dispatch("login");
          sessionStorage.setItem("token", res.token);
          this.$router.push(`/${roleMap[res.data.roleId - 1]}`);
        })
        .catch(() => {
          this.$message.error("邮箱或者密码填写错误，请重新填写");
        });
    }
  },
  watch: {
    loginDisplay: {
      handler(val) {
        this.loginDialogVisible = val;
      },
      immediate: true
    }
  }
};
</script>

<style lang="scss" scoped>
.home-login-dialog {
  $padding: 5%;
  .email,
  .pwd {
    @include flex(all, center, center);
    padding: 0 $padding;
    &-text {
      width: 15%;
    }
    .input {
      flex: 1;
    }
  }
  .pwd {
    margin: 20px 0 30px;
  }
  .login {
    margin-bottom: 15px;
  }
  .login,
  .register {
    text-align: center;
    & > button {
      width: 60%;
    }
  }
}
</style>
