<template>
  <el-card class="hr-resume-manage">
    <span class="title" slot="header">简历管理</span>
    <el-table :data="filterData" class="table">
      <el-table-column label="姓名" align="center" prop="name" :width="150">
      </el-table-column>
      <el-table-column
        prop="state"
        label="状态"
        :width="100"
        align="center"
        :filters="[
          { text: '初筛', value: '初筛' },
          { text: '面试中', value: '面试中' },
          { text: '完成', value: '完成' }
        ]"
        :filter-method="filterState"
      >
      </el-table-column>
      <el-table-column label="岗位名称" :width="200" align="center" prop="job">
      </el-table-column>
      <el-table-column align="right">
        <template slot="header">
          <el-input
            class="header-search-input"
            v-model="search"
            size="small"
            placeholder="输入名字搜索"
          />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >查看简历</el-button
          >
          <el-button
            size="mini"
            type="primary"
            @click="handleDelete(scope.$index, scope.row)"
            >面试记录</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <Pagination
      :hasCard="false"
      class="pagination"
      :total="100"
      :pageSize="10"
    />
  </el-card>
</template>

<script>
import { Pagination } from "@/components";

export default {
  name: "HrResumeManage",
  components: {
    Pagination
  },
  data() {
    return {
      tableData: [
        {
          name: "李宝伟",
          state: "初筛",
          job: "web前端开发"
        },
        {
          name: "yellow rainbow",
          state: "初筛",
          job: "web前端开发"
        },
        {
          name: "李宝伟",
          state: "初筛",
          job: "web前端开发"
        }
      ],
      search: ""
    };
  },
  methods: {
    handleEdit(index, row) {
      this.$router.push("/hr/resume-details");
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    filterState(value, row) {
      console.log(value, row);
      return row.state === value;
    }
  },
  computed: {
    filterData() {
      return this.tableData.filter(
        data =>
          !this.search ||
          data.name.toLowerCase().includes(this.search.toLowerCase())
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.hr-resume-manage {
  .title {
    @extend .font-title-1;
  }
  .header-search-input {
    width: 250px;
  }
  .pagination {
    margin: 20px 0 10px;
  }
}
</style>
