<template>
  <common-main class="home-main">
    <home-login
      class="login"
      @handleLoginBtn="loginDisplay = true"
    ></home-login>
    <home-login-dialog :loginDisplay.sync="loginDisplay" />
  </common-main>
</template>

<script>
import CommonMain from "@/views/components/home/HomeMain";
import HomeLogin from "./HomeLogin";
import HomeLoginDialog from "./HomeLoginDialog";

export default {
  name: "HomeMain",
  components: {
    HomeLogin,
    HomeLoginDialog,
    CommonMain
  },
  data() {
    return {
      loginDisplay: false
    };
  },
  created() {
    if (this.$route.query.login) {
      this.loginDisplay = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.home-main {
  width: $centerWidth;
  margin: $headerHeight + 10px auto;
}
</style>
