<template>
  <div class="question-common-tag">
    <el-tag
      class="tag"
      :closable="!readOnly"
      v-for="(item, index) in tagList"
      :key="item.id"
      :type="colorList[index % 4]"
      @close="handleClose(item.id)"
    >
      {{ item.label }}
    </el-tag>
    <span v-if="!readOnly">
      <el-input
        class="input-new-tag"
        v-if="inputVisible"
        v-model="inputValue"
        ref="saveTagInput"
        size="small"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm"
      >
      </el-input>
      <el-dropdown trigger="click" v-else @command="handleCommand">
        <el-button class="button-new-tag" size="small">+ New Tag</el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="newTag">新建标签</el-dropdown-item>
          <el-dropdown-item
            v-for="item in totalTagList"
            :command="item.label"
            :key="item.id"
            >{{ item.label }}</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </span>
  </div>
</template>

<script>
export default {
  name: "questionCommonTag",
  props: {
    TagItems: {
      type: Array,
      default: () => []
    },
    readOnly: {
      type: Boolean,
      default: true
    },
    totalTag: {
      type: Array,
      default: () => []
    }
  },
  components: {},
  data() {
    return {
      inputVisible: false,
      index: -1,
      inputValue: "",
      tagList: this.TagItems,
      // totalTagList: [],
      colorList: ["success", "info", "warning", "danger"]
    };
  },
  computed: {
    totalTagList() {
      return JSON.parse(JSON.stringify(this.totalTag));
    }
  },
  watch: {
    TagItems: function() {
      this.tagList = this.TagItems;
    }
  },
  methods: {
    handleClose(tagId) {
      let index = this.tagList.findIndex(item => item.id == tagId);
      if (tagId > 0) {
        this.totalTagList.push(this.tagList[index]);
      }
      this.tagList.splice(index, 1);
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.tagList.push({ id: this.index--, label: inputValue });
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    handleCommand(command) {
      if (command === "newTag") {
        this.inputVisible = true;
        this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      } else {
        if (command) {
          let index = this.totalTagList.findIndex(
            item => item.label == command
          );
          this.tagList.push(this.totalTagList[index]);
          this.totalTagList.splice(index, 1);
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.question-common-tag {
  .tag {
    margin: 5px 10px 5px 0px;
  }
  .input-new-tag {
    width: 90px;
  }
}
</style>
